.Submit {
  box-shadow: 2px 4px 0px 0px rgba(194, 97, 0, 0.918);
  background: linear-gradient(to bottom, #ededed 5%, #bab1ba 100%);
  background-color: #ededed;
  border-radius: 15px;
  border: 1px solid #d6bcd6;
  display: inline-block;
  cursor: pointer;
  color: #3a8a9e;
  font-family: Arial;
  font-size: 17px;
  padding: 5px 2px;
  text-decoration: none;
  text-shadow: 0px 1px 0px #e1e2ed;
}
.Submit:hover {
  background: linear-gradient(to bottom, #bab1ba 5%, #ededed 100%);
  background-color: #bab1ba;
}
.Submit:active {
  position: relative;
  top: 1px;
}

.input {
  height: 25px;
  margin: 20px;
  border-radius: 20px;
  box-shadow: 2px 4px 0px 0px rgba(194, 97, 0, 0.918);
}

.Reset {
  box-shadow: 2px 4px 0px 0px rgba(194, 97, 0, 0.918);
  background: linear-gradient(to bottom, #ededed 5%, #bab1ba 100%);
  background-color: #ededed;
  border-radius: 15px;
  border: 1px solid #d6bcd6;
  display: inline-block;
  cursor: pointer;
  color: #3a8a9e;
  font-family: Arial;
  font-size: 17px;
  padding: 5px 2px;
  text-decoration: none;
  text-shadow: 0px 1px 0px #e1e2ed;
}
