@import url(https://fonts.googleapis.com/css?family=Boogaloo&display=swap);
.container {
  position: relative;
  width: 400px;
  height: 300px;
  background-color: black;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px;
}

body {
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
}

h1,
h2,
h3 {
  color: rgba(194, 97, 0, 0.918);
  font-family: "Boogaloo";
}

.completed-orders {
  position: absolute;
  top: 250px;
  width: 400px;
  background-color: black;
  justify-content: center;
  padding: 10px;
}
.orders {
  display: flex;
}

.delete {
  background-color: black;
  border-radius: 6px;
  border: 1px solid rgba(194, 97, 0, 0.918);
  display: inline-block;
  cursor: pointer;
  color: rgba(194, 97, 0, 0.918);
  font-family: "Boogaloo";
  font-size: 15px;
  font-weight: bold;
  text-decoration: none;
  text-shadow: 0px -1px 0px #5b6178;
  display: flex;
  justify-content: center;
  height: 30px;
  width: 30px;
  margin-top: 20px;
  margin-left: 20px;
}
.delete:hover {
  background-color: #ed3443;
}

.hidedelete {
  display: none;
}

.hidedelete:hover {
  display: none;
}

.Submit {
  box-shadow: 2px 4px 0px 0px rgba(194, 97, 0, 0.918);
  background: -webkit-gradient(linear, left top, left bottom, color-stop(5%, #ededed), to(#bab1ba));
  background: linear-gradient(to bottom, #ededed 5%, #bab1ba 100%);
  background-color: #ededed;
  border-radius: 15px;
  border: 1px solid #d6bcd6;
  display: inline-block;
  cursor: pointer;
  color: #3a8a9e;
  font-family: Arial;
  font-size: 17px;
  padding: 5px 2px;
  text-decoration: none;
  text-shadow: 0px 1px 0px #e1e2ed;
}
.Submit:hover {
  background: -webkit-gradient(linear, left top, left bottom, color-stop(5%, #bab1ba), to(#ededed));
  background: linear-gradient(to bottom, #bab1ba 5%, #ededed 100%);
  background-color: #bab1ba;
}
.Submit:active {
  position: relative;
  top: 1px;
}

.input {
  height: 25px;
  margin: 20px;
  border-radius: 20px;
  box-shadow: 2px 4px 0px 0px rgba(194, 97, 0, 0.918);
}

.Reset {
  box-shadow: 2px 4px 0px 0px rgba(194, 97, 0, 0.918);
  background: -webkit-gradient(linear, left top, left bottom, color-stop(5%, #ededed), to(#bab1ba));
  background: linear-gradient(to bottom, #ededed 5%, #bab1ba 100%);
  background-color: #ededed;
  border-radius: 15px;
  border: 1px solid #d6bcd6;
  display: inline-block;
  cursor: pointer;
  color: #3a8a9e;
  font-family: Arial;
  font-size: 17px;
  padding: 5px 2px;
  text-decoration: none;
  text-shadow: 0px 1px 0px #e1e2ed;
}

